import React, { useEffect, useState } from 'react'
import { connect } from "react-redux"
// import { Row, Col } from 'antd';

import { LayoutLottery, TitleBox } from "components";
import { StepIconBox, Steps } from 'moduls'
import "./style.scss";
// import { isArray } from 'lodash'



const Register = ({
    onGetAllBanks
}) => {
    const [step, SetStep] = useState(1)


    useEffect(() => {
        onGetAllBanks()
    }, [])
    const onNextStep = async (values = 1) => {
        SetStep(values)
    }
    return (
        <LayoutLottery>
            <div className="page-register container-cst">
                <TitleBox name="회원가입" />
                <StepIconBox step={step - 1} />
                <Steps onNextStep={onNextStep} step={step} />
            </div>
        </LayoutLottery>
    )
}

export default connect(null,
    ({ common: { onGetAllBanks } }) => {
        return {
            onGetAllBanks
        };
    }
)(Register);
